@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400&family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
#root {
    height: 100%;
}

.Home {
    height: 100%;
    background-image: url(/hux-financial/static/media/home-bg.761c272b.jpg);
    background-position: center;
    background-size: cover;
}

.pri-bg {
    background-color: #d31145;
}

.pri-font {
    font-family: "Karla", sans-serif !important;
}

.sec-font {
    font-family: "Montserrat", sans-serif !important;
}

.translucent-black {
    background-color: rgba(50, 50, 50, 0.7);
}

/* NavBar */

.navbar-nav .active {
    border-bottom: 1px solid white;
}

.navbar-nav a:hover {
    font-weight: bold;
    text-decoration: none !important;
    border-bottom: 1px solid white;
}

/* NavBar Mobile */
.navbar-mobile #logo {
    z-index: 6;
}

.navbar-mobile .btn-outline-dark {
    border: none !important;
}

.navbar-mobile .btn-outline-dark:hover {
    background-color: transparent;
}

.navbar-mobile .btn-outline-dark:active {
    background-color: transparent !important;
}
.navbar-mobile .btn-outline-dark:focus {
    box-shadow: none !important;
}

.navbar-mobile .nav a.active {
    border-bottom: 1px solid white;
}

.navbar-mobile .nav a:hover {
    font-weight: bold;
    text-decoration: none !important;
    border-bottom: 1px solid white;
}

.navbar-mobile button {
    z-index: 6;
}

#navbar-mobile {
    background-color: #d31145;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 5;
    transform: translateX(100%);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    will-change: transform;
    transition: all 0.6s;
}

#navbar-mobile.active {
    transform: translateX(0%) !important;
}

/* Contact Form Styling */
.Contact input, .Contact textarea {
    font-family: "Montserrat", sans-serif !important;
    border: none;
    border-bottom: 1px solid grey;
    border-radius: 0px;
    margin-top: 1rem;
}

/* T & C Styling */
.t-and-c {
    font-size: 11px !important;
    font-family: "Karla", sans-serif !important;
    color:white;
}

/* XS Devices */
html {
    font-size: 12px !important;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
    html {
        font-size: 12px !important;
    }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
    html {
        font-size: 12px !important;
    }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
    html {
        font-size: 14px !important;
    }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
    html {
        font-size: 16px !important;
    }
}

